body,
html {
	font-family: 'Roboto', "Verdana", Times, serif;
	margin: 0;
	width: 100%;
	background-color: #f5f5f5;
}

.wow { 
	visibility: hidden; 
}

#project0 {
	height: 100vh;
	width: 100%;
	display: flex;
	justify-content: space-evenly;
	align-items: center;
}

@media only screen and (orientation: portrait) {
	#project0 {
		flex-direction: column;
		justify-content: space-evenly;
	}
}

@media only screen and (orientation: landscape) {
	#project0 {
		flex-direction: row;
	}
}



#blackLayer {
	display: none;
	position: fixed;
	width: 100%;
	height: 100%;
	left: 0;
	top: 0;
	background-color: black;
	opacity: 0.9;
	z-index: 990;
}
